import React, { useState, useRef, useEffect } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { FaTimes } from 'react-icons/fa'

interface StickyVideoPlayerProps {
  videoId: string
  playerRef?: React.MutableRefObject<any>
  onPlayerReady?: () => void
}

const StickyVideoPlayer: React.FC<StickyVideoPlayerProps> = ({ 
  videoId, 
  playerRef: externalPlayerRef,
  onPlayerReady 
}) => {
  const containerRef = useRef<HTMLDivElement>(null)
  const [isSticky, setIsSticky] = useState(false)
  const [isClosed, setIsClosed] = useState(false)
  const [containerTop, setContainerTop] = useState(0)

  useEffect(() => {
    if (isClosed) return

    // ì´ˆê¸° ìœ„ì¹˜ ì €ì¥
    if (containerRef.current && containerTop === 0) {
      const rect = containerRef.current.getBoundingClientRect()
      setContainerTop(rect.top + window.scrollY)
    }

    const handleScroll = () => {
      if (!containerRef.current) return

      const scrollY = window.scrollY
      const threshold = 150 // í”Œë ˆì´ì–´ê°€ í™”ë©´ ë°–ìœ¼ë¡œ ë‚˜ê°€ëŠ” ì„ê³„ê°’

      // ìŠ¤í¬ë¡¤ì´ í”Œë ˆì´ì–´ ì›ë˜ ìœ„ì¹˜ë¥¼ ë„˜ì–´ê°€ë©´ sticky í™œì„±í™”
      if (scrollY > containerTop + threshold && !isSticky) {
        setIsSticky(true)
      }

      // ë‹¤ì‹œ ì›ë˜ ìœ„ì¹˜ë¡œ ëŒì•„ì˜¤ë©´ sticky í•´ì œ
      if (scrollY <= containerTop + threshold && isSticky) {
        setIsSticky(false)
      }
    }

    window.addEventListener('scroll', handleScroll, { passive: true })
    handleScroll() // ì´ˆê¸° ì²´í¬

    return () => window.removeEventListener('scroll', handleScroll)
  }, [isSticky, containerTop, isClosed])

  // Sticky ëª¨ë“œ ë‹«ê¸°
  const handleClose = () => {
    setIsClosed(true)
    setIsSticky(false)
  }

  // ë°˜ì‘í˜• ìœ„ì¹˜ ê³„ì‚°
  const getStickyPosition = () => {
    const isMobile = window.innerWidth < 640
    if (isMobile) {
      return {
        top: '80px',
        left: '50%',
        transform: 'translateX(-50%)',
        width: 'calc(100% - 40px)',
        maxWidth: '90vw',
      }
    } else {
      return {
        top: '80px',
        left: '280px', // ì‚¬ì´ë“œë°” ìš°ì¸¡
        width: '400px',
        maxWidth: 'calc(100vw - 320px)', // ì‚¬ì´ë“œë°” + ì—¬ë°±
      }
    }
  }

  return (
    <>
      {/* ì›ë˜ í”Œë ˆì´ì–´ ì˜ì—­ */}
      <div ref={containerRef} className={`relative ${isSticky ? 'invisible' : 'visible'}`}>
        {/* Sticky ëª¨ë“œì¼ ë•Œ ì›ë˜ ìë¦¬ ê³µê°„ ìœ ì§€ */}
        {isSticky && (
          <div className="aspect-video bg-gray-100 dark:bg-gray-800 rounded-xl flex items-center justify-center">
            <p className="text-gray-500 dark:text-gray-400 text-sm">
              ì˜ìƒì´ ìƒë‹¨ì—ì„œ ì¬ìƒ ì¤‘ì…ë‹ˆë‹¤
            </p>
          </div>
        )}

        {/* ì›ë˜ í¬ê¸° í”Œë ˆì´ì–´ */}
        {!isSticky && (
          <div className="aspect-video rounded-xl overflow-hidden shadow-2xl bg-black relative">
            <div 
              ref={externalPlayerRef}
              className="w-full h-full"
            />
          </div>
        )}
      </div>

      {/* Sticky í”Œë ˆì´ì–´ (í™”ë©´ ê³ ì •) */}
      <AnimatePresence>
        {isSticky && !isClosed && (
          <motion.div
            initial={{ opacity: 0, scale: 0.9, y: -20 }}
            animate={{ opacity: 1, scale: 1, y: 0 }}
            exit={{ opacity: 0, scale: 0.9, y: -20 }}
            transition={{ type: 'spring', damping: 20, stiffness: 300 }}
            className="fixed z-50"
            style={getStickyPosition()}
          >
            <div className="bg-black rounded-lg shadow-2xl overflow-hidden border-2 border-blue-500">
              {/* í—¤ë” */}
              <div className="bg-gray-900 px-3 py-2 flex items-center justify-between">
                <span className="text-white text-xs font-medium flex items-center gap-2">
                  <div className="w-2 h-2 rounded-full bg-red-500 animate-pulse"></div>
                  ì¬ìƒ ì¤‘
                </span>
                <button
                  onClick={handleClose}
                  className="p-1.5 hover:bg-red-600 rounded text-white transition-colors"
                  title="ë‹«ê¸°"
                >
                  <FaTimes size={14} />
                </button>
              </div>

              {/* í”Œë ˆì´ì–´ - iframeì€ ì—¬ê¸°ì„œ ì§ì ‘ ìƒì„±í•˜ì§€ ì•Šê³  YouTube IFrame API ì‚¬ìš© */}
              <div className="aspect-video bg-black">
                <iframe
                  src={`https://www.youtube.com/embed/${videoId}?enablejsapi=1&rel=0&modestbranding=1`}
                  title="YouTube Sticky Player"
                  allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                  allowFullScreen
                  className="w-full h-full"
                  style={{ border: 'none' }}
                />
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  )
}

export default StickyVideoPlayer

